.equal-height .product {
    height: 100%;
}

.product-shadow {
    transition: box-shadow 0.3s;
}

.product-shadow:hover {
    box-shadow: 0 15px 25px -20px rgba(0, 0, 0, 0.5);
}

.product-label-round .product-label {
    padding: 0.5rem 1.1rem;
    border-radius: 1rem;
}

.text-center .product-details {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
}

.product-wrap {
    margin-bottom: 2rem;
}

.product {
    position: relative;
    overflow: hidden;
    font-family: circularstd-medium;
    font-size: 1.4rem;
    color: #999;
    transition: 0.3s;
    border: 1px solid var(--base-color);
    border-radius: 20px;
    padding: 20px;
}

.product:hover .product-action,
.product:hover .product-action-vertical {
    visibility: visible;
    opacity: 1;
}

.product-media:hover img:first-child {
    opacity: 0;

    .title-bottom-left {
        display: block !important;
    }

    .description-bottom-left {
        display: block !important;
    }
}

.product-media:hover img:first-child {
    opacity: 0;

}

.product-media:hover img:last-child {
    opacity: 1;
}

.product-media:hover img:last-child {
    opacity: 1;

    .title-bottom-left {
        display: block;
    }

    .description-bottom-left {
        display: block;
    }
}

.product-media {
    position: relative;
    margin-bottom: 0;
    transition: box-shadow 0.3s;
}

.product-media img {
    display: block;
    width: 100%;
    height: auto;
    transition: all 0.5s;
}

.product-media img:last-child {
    position: absolute;
    opacity: 0;
    left: 0;
    right: 0;
    top: 0;
    left: 0;
    object-fit: cover;
}

.product-media img:first-child {
    position: relative;
    opacity: 1;

}

.product-label-group {
    display: flex;
    flex-direction: column;
    position: absolute;
    right: 1rem;
    top: 1rem;
    max-width: 6rem;
}

.product-label {
    display: inline-block;
    text-align: center;
    margin-bottom: 0.5rem;
    padding: 0.5rem 1.1rem;
    font-size: 12px;
    border-radius: 0px 0px 10px 10px;
    font-weight: 600;
    line-height: 20px;
    color: #fff;
    text-transform: uppercase;
}

.product-label.label-new {
    background: #4dae67;
}

.product-label.label-sale {
    background: #000;
}

.product-label.label-top {
    background: #4dae67;
}

.product-label.label-stock {
    background: #999;
}

.btn-product-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 3.6rem;
    height: 3.6rem;
    margin-bottom: 0.5rem;
    border: 1px solid #eee;
    border-radius: 50%;
    background-color: #fff;
    color: #999;
    font-size: 1.6rem;
    font-weight: 700;
    transition: border-color 0.3s, color 0.3s, background-color 0.3s;
}

.btn-product-icon:hover {
    border-color: var(--base-color);
    color: #fff;
    background-color: var(--base-color);
}

.btn-product-icon:not(.btn-wishlist) i {
    margin-bottom: 2px;
}

.btn-product-icon.btn-quickview i {
    font-size: inherit;
}

.btn-product-icon.btn-compare i {
    font-size: 1.8rem;
}

.btn-product {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;
    padding: 1.14em 0;
    background-color: #4dae67;
    color: var(--black);
    font-family: circularstd-medium;
    font-size: 1.4rem;
    font-weight: 100;
    line-height: 1.2;
    letter-spacing: -0.025em;
    text-transform: uppercase;
    transition: opacity 0.3s;
}

.btn-product:hover {
    color: #fff;
}

.btn-quickview {
    opacity: 0.8;
    transition: opacity 0.3s;
}

.btn-quickview:hover {
    opacity: 1;
}

.btn-quickview i {
    font-size: 1.4rem;
}

.btn-wishlist {
    line-height: 1;
}

.btn-wishlist.loading::before,
.btn-wishlist.loading::after {
    z-index: 1;
}

#ingredients_section .product-action-vertical {
    display: block;
}

.product-action-vertical {
    position: absolute;
    bottom: 15px;
    right: 15px;
    transition: opacity 0.3s, visibility 0.3s;
    opacity: 0;
    visibility: hidden;
    /*display: none;*/
}

.product-action-vertical .btn-wishlist.loading {
    overflow: hidden;
}

.product-action {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    background-color: transparent;
    z-index: 10;
    transition: opacity 0.3s, visibility 0.3s;
    opacity: 0;
}

.product-details {
    position: relative;
    background: transparent;
    padding-top: 1.4rem;
    padding-bottom: 2rem;
}

.product-details>.btn-wishlist {
    position: absolute;
    top: 1.2rem;
    right: 0;
    color: #999;
    z-index: 1;
    font-size: 1.6rem;
}

.product-details>.btn-wishlist i {
    display: inline-block;
    margin: 0;
    transition: transform 0.3s;
}

.product-details>.btn-wishlist:hover {
    color: #4dae67;
}

.product-details>.btn-wishlist:hover i {
    transform: rotateY(180deg);
}

.product-details> :last-child {
    margin-bottom: 0;
}

.product-details .product-action {
    position: relative;
    opacity: 1;
    visibility: visible;
}

.product-cat {
    margin-bottom: 0.5rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1;
    text-transform: uppercase;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.product-cat a:hover {
    color: #4dae67;
}

.product-name {
    margin-bottom: 0.3rem;
    font-family: inherit;
    font-size: 20px;
    line-height: 30px;
    text-align: center;
    font-weight: 100;
    letter-spacing: -0.01em;
    color: var(--color-text);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-family: circularstd-medium;
}

.product-name a:hover {
    color: #4dae67;
}

.product-price {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    display: block;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 0.3rem;
    font-size: 1.6rem;
    font-weight: 600;
    line-height: 1.86;
    color: #222;
}

.product-price .old-price {
    color: #aaa;
}

.product-price .new-price {
    margin-right: 1rem;
    text-decoration: none;
}

.dark-theme .old-price {
    color: #666;
}

.dark-theme .product.product-with-qty .quantity,
.dark-theme .product.product-with-qty button {
    color: #999;
}

.dark-theme .rating-reviews {
    color: #999;
}

.dark-theme .ratings-full::before {
    color: #666;
}

.ratings-container {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 1px;
    line-height: 1.1;
    /*margin-bottom: 1.2rem;*/
    font-size: 11px;
    letter-spacing: 0.2em;
}

.ratings-full {
    cursor: pointer;
}

.ratings-full,
.ratings {
    position: relative;
    font-family: "riode";
}

.ratings-full+.rating-reviews {
    margin-left: 1rem;
}

.ratings-full::before {
    content: "" "" "" "" "";
    color: rgba(0, 0, 0, 0.16);
    font-family: "riode";
}

.ratings-full:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
}

.ratings {
    position: absolute;
    top: 0;
    left: 0;
    white-space: nowrap;
    overflow: hidden;
}

.ratings::before {
    content: "" "" "" "" "";
    color: #fd990b;
    font-family: "riode";
}

.rating-reviews {
    margin: 2px 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 1.1rem;
    color: #aaa;
    letter-spacing: 0;
    text-transform: capitalize;
}

.rating-reviews:hover {
    color: #4dae67;
}

.product-variations {
    display: flex;
}

.ratings-container+.product-variations {
    margin-top: -5px;
}

.product-variations>a:not(.size-guide) {
    display: inline-block;
    position: relative;
    margin: 3px 5px 3px 0;
    padding: 0 5px;
    border-radius: 2px;
    background-color: transparent;
    background-size: cover;
    font-size: 1.2rem;
    font-family: inherit;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    width: 2.6rem;
    height: 2.6rem;
    color: #222;
    transition: 0.3s;
    border: 1px solid #eee;
}

.product-variations>a:not(.size-guide):last-child {
    margin-right: 0;
}

.product-variations .color:not(.active) {
    border: none;
}

.product-variations>a.active,
.product-variations>a:not(.size-guide):hover {
    border: 1px solid;
    box-shadow: inset 0 0 0 2px #fff;
}

.product-details .product-action,
.product-hide-details .product-action {
    position: relative;
    opacity: 1;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.product-details .product-action .btn-wishlist,
.product-hide-details .product-action .btn-wishlist {
    position: static;
}

.product-details .btn-cart,
.product-hide-details .btn-cart {
    display: block;
    max-width: 14rem;
    line-height: 2.9;
    padding: 0 0.6em;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #222;
    background-color: transparent;
    border: 2px solid #ccc;
    margin-right: 5px;
    border-radius: 0.3rem;
}

.product-details .btn-cart i,
.product-hide-details .btn-cart i {
    margin-right: 0.8rem;
    font-size: 1.8rem;
}

.product-details .btn-cart,
.product-hide-details .btn-cart {
    flex: 1;
    height: 4.2rem;
    font-size: 1.3rem;
    transition: color 0.3s, background-color 0.3s, border-color 0.3s;
    z-index: 1;
}

.product-details .btn-quickview,
.product-hide-details .btn-quickview {
    margin: 0 0 0 1.6rem;
}

.product-details .btn-product-icon,
.product-hide-details .btn-product-icon {
    width: auto;
    border: 0;
    background-color: transparent;
    color: #222;
    transition: color 0.3s, background-color 0.3s, visibility 0.3s;
}

.product-details .btn-product-icon i,
.product-hide-details .btn-product-icon i {
    font-size: 1.8rem;
}

.product-details .btn-product-icon:hover,
.product-hide-details .btn-product-icon:hover {
    color: #4dae67;
}

.product:hover .product-action .btn-cart {
    color: #fff;
    background-color: #444;
    border-color: #444;
}

@media screen and (-ms-high-contrast: active),
(-ms-high-contrast: none) {

    .product-hide-details .btn-cart,
    .product-details .btn-cart {
        flex: 0 0 12.3rem;
    }
}

@media screen and (-ms-high-contrast: active) and (max-width: 575px),
(-ms-high-contrast: none) and (max-width: 575px) {

    .product-hide-details .btn-cart,
    .product-details .btn-cart {
        flex: 0 0 9.5rem;
    }
}

.product-classic .btn-quickview {
    opacity: 1;
}

.product-classic .btn-quickview i:before {
    content: "";
}

.product-classic .btn-wishlist {
    margin: 0 0 0 1.6rem;
}

.product-classic .btn-wishlist,
.product-classic .btn-quickview {
    transform: translateX(-200%);
    opacity: 0;
    visibility: hidden;
    transition: transform 0.3s, opacity 0.3s, visibility 0.3s, color 0.3s, background-color 0.3s, border-color 0.3s;
}

.product-classic .btn-quickview {
    transition: transform 0.5s, opacity 0.5s, visibility 0.5s, color 0.3s, background-color 0.3s, border-color 0.3s;
}

.product-classic:hover .btn-wishlist,
.product-classic:hover .btn-quickview {
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
}

.product-classic .product-details {
    padding-left: 0;
}

.product-classic .ratings-container {
    margin-bottom: 1.5rem;
}

.product-slideup-content {
    overflow: hidden;
}

.product-slideup-content .product-hide-details {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    transition: transform 0.3s;
    background-color: #fff;
}

.product-slideup-content .product-details {
    padding-bottom: 1px;
    background-color: #fff;
    transition: transform 0.3s;
}

.product-slideup-content .btn-wishlist,
.product-slideup-content .btn-quickview {
    opacity: 0;
    visibility: hidden;
    transition: transform 0.4s, opacity 0.3s, visibility 0.3s, color 0.3s, background-color 0.3s;
}

.product-slideup-content .btn-quickview {
    transform: translateX(-200%);
}

.product-slideup-content .btn-wishlist {
    transform: translateX(200%);
}

.product-slideup-content:hover .btn-wishlist,
.product-slideup-content:hover .btn-quickview {
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
}

.product-slideup .product-details {
    padding-bottom: 0;
    background-color: #fff;
    transition: transform 0.3s;
}

.product-slideup .ratings-container {
    margin-bottom: 0;
}

.product-slideup .btn-wishlist,
.product-slideup .btn-quickview {
    opacity: 0;
    visibility: hidden;
    transition: transform 0.4s, opacity 0.3s, visibility 0.3s, color 0.3s, background-color 0.3s;
}

.product-slideup .btn-quickview {
    transform: translateX(-200%);
}

.product-slideup .btn-wishlist {
    transform: translateX(200%);
}

.product-slideup .product-action {
    display: flex;
    position: absolute;
    padding-top: 1.5rem;
    top: 100%;
    bottom: auto;
    visibility: hidden;
    opacity: 0;
}

.product-slideup:hover {
    transform: translateY(-58px);
}

.product-slideup:hover .btn-wishlist,
.product-slideup:hover .btn-quickview {
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
}

.product-slideup:hover .product-action {
    visibility: visible;
    opacity: 1;
}

.product-slideup .btn-cart,
.product-slideup-content .btn-cart {
    margin-right: 0;
}

.product-slideup .btn-wishlist,
.product-slideup-content .btn-wishlist {
    position: static;
    margin: 0 1.6rem 0 0;
}

.split-line {
    overflow: hidden;
}

.product-image-gap {
    padding: 0.9rem;
    border: 1px solid #eee;
    background-color: #fff;
}

.product-image-gap .product-details {
    padding: 1.8rem 1rem 1rem;
}

.product-image-gap .ratings-container {
    margin-bottom: 0;
}

.product-image-gap:hover {
    box-shadow: 0 5px 30px rgba(0, 0, 0, 0.05);
}

@media (max-width: 479px) {
    .product-image-gap .product-details {
        padding: 1.8rem 0 1rem;
    }
}

.product-list {
    display: flex;
    align-items: center;
    margin-bottom: 2rem;
}

.product-list .product-media {
    flex: 0 0 28rem;
    max-width: 28rem;
    margin: 0 2rem 0 0;
}

.product-list .product-details {
    padding: 0;
    flex: 1;
}

.product-list .product-label {
    font-size: 1.1rem;
}

.product-list .btn-quickview {
    opacity: 1;
}

.product-list .btn-wishlist {
    margin: 0 0 0 0.8rem;
}

.product-list .btn-wishlist,
.product-list .btn-quickview {
    transform: translateX(-200%);
    opacity: 0;
    visibility: hidden;
    transition: transform 0.3s, opacity 0.3s, visibility 0.3s, color 0.3s, background-color 0.3s;
}

.product-list .btn-quickview {
    transition: transform 0.5s, opacity 0.5s, visibility 0.5s, color 0.3s, background-color 0.3s;
}

.product-list .ratings-container {
    margin-bottom: 0.8rem;
}

.product-list .product-name {
    font-size: 1.8rem;
    font-weight: 600;
    letter-spacing: -0.025em;
    color: #222;
}

.product-list .product-price {
    font-size: 1.8rem;
    font-weight: 600;
}

.product-list .product-short-desc {
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    margin-bottom: 2rem;
    text-transform: none;
    font-size: 1.4rem;
    line-height: 1.78;
    color: #666;
}

.product-list:hover .btn-quickview,
.product-list:hover .btn-wishlist {
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
}

.product-list-sm {
    display: flex;
    align-items: center;
    margin-bottom: 2rem;
}

.product-list-sm .product-media {
    flex: 0 0 10rem;
    max-width: 10rem;
    margin: 0 1rem 0 0;
}

.product-list-sm .product-details {
    flex: 0 0 calc(100% - 10rem);
    max-width: calc(100% - 10rem);
    padding: 0;
}

.product-list-sm .product-name {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    margin-bottom: 0.7rem;
    white-space: normal;
    color: #222;
    padding-left: 0;
}

.product-list-sm .product-name:hover {
    color: #4dae67;
}

.product-list-sm .ratings-container {
    margin: 0;
}

.product-list-sm .product-price {
    font-size: 1.4rem;
}

.product-list-sm+.product-list {
    margin-top: 2rem;
}

.product-list-sm .btn-cart {
    margin-top: 2rem;
    height: 3.3rem;
    max-width: 11.7rem;
    border: 1px solid #d7d7d7;
    color: #fd990b;
    background: transparent;
    font-size: 1.2rem;
}

.product-list-sm .btn-cart:hover {
    border-color: #fd990b;
    background: #fd990b;
}

.product.text-center .product-cat {
    padding: 0;
}

