.product.cart-full .btn-cart {
    max-width: none;
    width: 100%;
}

.product.product-with-qty .product-details {
    padding-left: 0;
    padding-right: 0;
}

.product.product-with-qty .btn-cart {
    max-width: 20rem;
    margin-right: 0;
}

.product.product-with-qty .btn-cart i {
    display: none;
}

.product.product-with-qty .product-quantity {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 2rem;
}

.product.product-with-qty button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    color: #333;
    font-size: 1rem;
    width: 24px;
    height: 24px;
    border-radius: 0.3rem;
    border: none;
    background: #f4f4f4;
